import { UserLocalizationOptionVM, LocalizationOption } from '../types';

export const findLanguageRegionOption = (
  localizationOptionsConfig: LocalizationOption[] = [],
  preferredLanguage?: string,
): UserLocalizationOptionVM =>
  localizationOptionsConfig.find((item) => item.value === preferredLanguage) || {
    isDefault: true,
    value: typeof preferredLanguage === 'string' ? preferredLanguage : undefined,
    label: '',
  };
